.subscribeRow{
    display: flex;
    border-top: 0;
    border-bottom: 1px;
    border-right: 0;
    border-left: 0;
    border-style: solid;
    border-color: var(--button_bot_shadow);
    margin-top: 5px;
    background-color: var(--background_content);
    padding: 12px;
    color: var(--accent);
    -webkit-transition-property: background-color;
    transition-property: background-color;
    -webkit-transition-duration: 0.2s;
            transition-duration: 0.2s;
}
.subscribeRow:active{
    background-color: var(--background_light);
}
.subscribeRow:hover{
    background-color: var(--background_page);
}
.subscribeIconExtender{
    display: flex;
    float: left;
    align-items: center;
    justify-content: center;
    width: 25px;
}
.subscribeRowFullContent{
    display:flex;
    width:100%;
    justify-content:space-around;
}
.subscribeRowContent{
    display: flex;
    max-width: 630px;
    flex-wrap: wrap;
    justify-content : space-around;
}
.subscribeRowTitleNameContainer{
    display: flex;
    float: left;
    align-items: center;
    justify-content: left;
    margin-left: 7px;
    width: 310px;
    position: relative;
}
.subscribeRowButtonContainer{
    display: flex;
    margin-top: 5px;
    align-items: center;
}
.subscribeRowButton{
    min-width: 149px;
    margin-left: 6px;
    height: 30px;
}
.fullTitleInformation{
    background-color: var(--background_light);
    border-top: 0;
    border-bottom: 1px;
    border-right: 0;
    border-left: 0;
    border-style: solid;
    border-color: var(--button_bot_shadow);
    justify-content : space-around;
}
@media screen and (max-width: 700px) and (min-width: 320px){
    .fullTitleInformationContainer{
        width:315px !important;
    }
    .fullTitleInformationImage{
        display:none !important;
    }
    .fullTitleInformationContent{
        padding-left:0 !important;
    }
}
.fullTitleInformationContainer{
    display:flex;
    width: 630px;
    left: 25px;
    padding-top:10px;
    padding-bottom: 10px;
}
.fullTitleInformationImage{
    float:left;
    width:160px;
    background-size: contain;
    background-repeat: no-repeat;
}
.fullTitleInformationContent{
    width: 100%;
    padding-left:10px;
    font-size: 13px;
    text-align: justify;
    color: var(--accent)
}
.fullTitleInformationContent b{
    font-size: 14px;
}

.fullTitleInformationSpinner{
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: normal !important;
}

.fullTitleInformationImageImg{
    width: 100%;
    height: auto;
}
.fullTitleInformationContentButton{
    display:flex;
}

.fullTextInformationAuthorLink{
    text-decoration:underline !important;
}

.statisticRowProgressContainer{
    margin-top: 5px;
    width:310px;
    align-items: center;
    display: grid;
}
.fullTextInformationIconFire{
    position: absolute;
    top: -3px;
    left: -8px;
}
